import React from "react"
import Title from "./projects/title"
import Text from "./projects/text"
import BigTitle from "./projects/bigTitle"
import ProjectLink from "./projectLink"
import TopBox from "./projects/topBox"
import pStyles from "../styles/projectLink.module.css"
import styles from "../styles/projects.module.css"
import polypImg from "../images/polyp6image.png"
import igemImg from "../images/igem6.png"
import inephemeraImg from "../images/inephemeraimage6.png"
import headlinesImg from "../images/headlinesimage6.png"

export default (props) => (
    <div>
        <TopBox/>

        <Title classname={styles.polypTitle}>Polyp</Title>
        <BigTitle>
            {props.bigTitle}
        </BigTitle>

        <Title classname={styles.situationTitle}>The situation</Title>
        <Text classname={styles.situationText}>
            {props.situationText}
        </Text>

        <Title classname={styles.didTitle}>What we did</Title>
        <Text classname={styles.didText}>
            {props.didText}
        </Text>

        <Title classname={styles.solutionTitle}>The solution</Title>
        <Text classname={styles.solutionText}>
            {props.solutionText}
        </Text>

        <SmallProjectLink name={props.nameOne} classname="one"/>
        <SmallProjectLink name={props.nameTwo} classname="two"/>
        <SmallProjectLink name={props.nameThree} classname="three"/>
    </div>
)

class SmallProjectLink extends React.Component{
    constructor(props){
        super(props);
        this.name = props.name;
        this.classname = props.classname;
    }

    render()
    {
        let background;
        let btext;
        let to;
        let children;

        if (this.name === "polyp") {
            background=polypImg
            btext="Polyp"
            to="/polyp/"
            children="Blockchain • Self Sovereign Ecosystems"
        }
        if (this.name === "igem") {
            background=igemImg
            btext="Psychobiosis"
            to="/igem/"
            children="Synthetic Biology • Psychobiotics"
        }
        if (this.name === "headlines") {
            background=headlinesImg
            btext="Urban Speculation"
            to="/headlines/"
            children="Urban Design • Architecture"
        }
        if (this.name === "inephemera") {
            background=inephemeraImg
            btext="General Futuristics"
            //to="/inephemera/"
            children="Analysis • Worldview"
        }
        if (this.classname === "one") {

            return (
                <ProjectLink
                    topname={pStyles.smallTop}
                    widthname={pStyles.smallWidth}
                    classname={pStyles.oneS}
                    background={background}
                    btext={btext}
                    to={to}>
                    {children}
                </ProjectLink>
            )
        }

        if (this.classname === "two") {
            return (
                <ProjectLink
                    topname={pStyles.smallTop}
                    widthname={pStyles.smallWidth}
                    classname={pStyles.twoS}
                    background={background}
                    btext={btext}
                    to={to}>
                    {children}
                </ProjectLink>
            )
        }
        if (this.classname === "three") {
            return (
                <ProjectLink
                    topname={pStyles.smallTop}
                    widthname={pStyles.smallWidth}
                    classname={pStyles.threeS}
                    background={background}
                    btext={btext}
                    to={to}>
                    {children}
                </ProjectLink>
            )
        }
    }
}