import React from "react"
import styles from "../styles/projectLink.module.css"
import {Link} from "gatsby"

export default (props) => (
    <div className={props.classname}>
        <Link to={props.to} className={styles.link}>
            <img src={props.background} alt="" className={props.widthname}/>
        </Link>
        <div className={`${styles.boldText} ${props.topname}`}> {props.btext} </div>
        <div className={`${styles.text} ${props.topname}`}> {props.children} </div>
    </div>
)